<template>
  <div class="container">
    <el-row>
      <Nav />
    </el-row>
    <el-row type="flex" justify="center">
      <!-- <el-col :span="1" :offset="1">
        <el-tooltip class="item" effect="dark" content="回到首页" placement="bottom">
      <i @click="handleback" class="el-icon-s-home"></i>
    </el-tooltip></el-col> -->
      <el-col :span="22">
        <el-tabs type="border-card" :stretch="stretch">
          <el-tab-pane label="历史打卡">
            <el-row>
              <el-col :span="20">
                <el-input placeholder="请输入查询关键字"></el-input>
              </el-col>
              <el-col :span="4">
                <el-button type="primary" style="width: 100%">查询</el-button>
              </el-col>
            </el-row>
            <br />
            <el-row>
              <el-table :data="historyTask" border style="width: 100%">
                <el-table-column fixed prop="taskTitle" label="任务标题" width="500"></el-table-column>
                <el-table-column prop="taskFrom" label="发布者" width="120"></el-table-column>
                <el-table-column prop="publicDate" label="发布日期" width="180"></el-table-column>
                <el-table-column prop="deadLine" label="截止日期" width="180"></el-table-column>
                <el-table-column prop="isLang" label="编程语言" width="120"></el-table-column>
                <el-table-column fixed="right" label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="搜索任务">
            <el-row>
              <el-col :span="20">
                <el-input v-model="searchTaskTitle" placeholder="请输入查询关键字"></el-input>
              </el-col>
              <el-col :span="4">
                <el-button type="primary" style="width: 100%" @click="handleSearchTask">查询</el-button>
              </el-col>
            </el-row>
            <br />
            <el-row>
              <el-table :data="searchTaskList" border style="width: 100%">
                <el-table-column fixed prop="taskTitle" label="任务标题" width="400"></el-table-column>
                <el-table-column prop="stuId" label="用户名" width="120"></el-table-column>
                <el-table-column prop="taskFrom" label="发布者" width="120"></el-table-column>
                <el-table-column prop="publicDate" label="发布日期" width="180"></el-table-column>
                <el-table-column prop="deadLine" label="截止日期" width="180"></el-table-column>
                <el-table-column prop="isLang" label="编程语言" width="120"></el-table-column>
                <el-table-column fixed="right" label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="搜索用户">
            <el-row>
              <el-col :span="20">
                <el-input v-model="searchStuID" placeholder="请输入查询关键字"></el-input>
              </el-col>
              <el-col :span="4">
                <el-button type="primary" style="width: 100%" @click="handleSearchUserTask">查询</el-button>
              </el-col>
            </el-row>
            <br />
            <el-row>
              <el-table :data="searchUserTask" border style="width: 100%" max-height="600">
                <el-table-column fixed prop="taskTitle" label="任务标题" width="500"></el-table-column>
                <el-table-column prop="taskFrom" label="发布者" width="120"></el-table-column>
                <el-table-column prop="publicDate" label="发布日期" width="180"></el-table-column>
                <el-table-column prop="deadLine" label="截止日期" width="180"></el-table-column>
                <el-table-column prop="isLang" label="编程语言" width="120"></el-table-column>
                <el-table-column fixed="right" label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Nav from './Nav'
export default {
  name: "index",
  components: {
    Nav
  },
  data() {
    return {
      stretch: true,
      historyTask: [], // 返回查询历史任务
      searchTaskTitle: "",
      searchTaskList: [],
      searchStuID: "",
      searchUserTask: [] // 返回查询用户信息
    };
  },
  mounted() {
    this.$axios({
      url: "/api/userLogin/postFindCode",
      method: "post",
      data: {
        stuId: this.$store.state.userInfo.username
      }
    })
      .then(res => {
        console.log(res);
        this.historyTask = [];
        if (res.data.err === 0) {
          this.$message({
            message: "获取历史任务成功",
            type: "success"
          });
          for (let i = 0; i < res.data.msg.length; i++) {
            if (res.data.msg[i] != null) {
              // console.log(res.data.msg[i]);
              let oneTask = {};
              oneTask = res.data.msg[i];
              this.historyTask.push(oneTask);
            }
          }
          for (let j = 0; j < this.historyTask.length; j++) {
            this.historyTask[j].publicDate = this.$moment(
              this.historyTask[j].publicDate
            ).format("YYYY-MM-DD HH:mm:ss");
            this.historyTask[j].deadLine = this.$moment(
              this.historyTask[j].deadLine
            ).format("YYYY-MM-DD HH:mm:ss");
          }
          return this.historyTask;
        } else {
          this.$message({
            message: res.data.msg,
            type: "err"
          });
        }
      })
      .catch(err => {
        alert("网络异常，请重试！");
      });
  },
  methods: {
    handleback() {
      this.$router.push({ path: '/stuHome'});
    },
    handleSearchTask() {
      this.$axios({
        url: "/api/userLogin/postFindTaskByTitle",
        method: "post",
        data: {
          taskTitle: this.searchTaskTitle
        }
      })
        .then(res => {
          console.log(res);

          if (res.data.err === 0) {
            this.$message({
              message: "查询任务成功",
              type: "success"
            });
            this.searchTaskList = [];
            let stuId = "";
            for (var i = 0; i < res.data.msg.length; i++) {
              stuId = res.data.msg[i].stuId;
              for (var j = 0; j < res.data.msg[i].tasks.length; j++) {
                // console.log(j);
                if (res.data.msg[i].tasks[j]) {
                  // 小心null的坑，null 无法读取属性

                  if (
                    res.data.msg[i].tasks[j].taskTitle === this.searchTaskTitle
                  ) {
                    res.data.msg[i].tasks[j].publicDate = this.$moment(
                      res.data.msg[i].tasks[j].publicDate
                    ).format("YYYY-MM-DD HH:mm:ss");
                    res.data.msg[i].tasks[j].deadLine = this.$moment(
                      res.data.msg[i].tasks[j].deadLine
                    ).format("YYYY-MM-DD HH:mm:ss");
                    res.data.msg[i].tasks[j].stuId = stuId;
                    this.searchTaskList.push(res.data.msg[i].tasks[j]);
                  }
                }
              }
            }
            console.log(this.searchTaskList);
            return this.searchTaskList;
          } else {
            this.$message({
              message: res.data.msg,
              type: "err"
            });
          }
        })
        .catch(err => {
          alert("网络异常，请重试！");
        });
    },
    handleSearchUserTask() {
      this.$axios({
        url: "/api/userLogin/postFindCode",
        method: "post",
        data: {
          stuId: this.searchStuID
        }
      })
        .then(res => {
          console.log(res);
          this.searchUserTask = [];
          if (res.data.err === 0) {
            this.$message({
              message: "获取用户信息成功",
              type: "success"
            });
            for (let i = 0; i < res.data.msg.length; i++) {
              if (res.data.msg[i] != null) {
                // console.log(res.data.msg[i]);
                let oneTask = {};
                oneTask = res.data.msg[i];
                this.searchUserTask.push(oneTask);
              }
            }
            for (let j = 0; j < this.searchUserTask.length; j++) {
              this.searchUserTask[j].publicDate = this.$moment(
                this.searchUserTask[j].publicDate
              ).format("YYYY-MM-DD HH:mm:ss");
              this.searchUserTask[j].deadLine = this.$moment(
                this.searchUserTask[j].deadLine
              ).format("YYYY-MM-DD HH:mm:ss");
            }
            return this.searchUserTask;
          } else {
            this.$message({
              message: res.data.msg,
              type: "err"
            });
          }
        })
        .catch(err => {
          alert("网络异常，请重试！");
        });
    }
  }
};
</script>

<style lang="scss" scoped>
  .container {
    .el-icon-s-home {
      font-size: 300%;
      cursor: pointer;
    color: #409eff;
    }
  }
